header {
  width: 100%;
  margin-bottom: 7px;
  box-shadow: 0 0 7px #004992;
  background: linear-gradient(rgb(243, 242, 242), #d0d4d4, #bababa, #b1b1b1); }

.navLink {
  color: #ffffff;
  padding: 5px;
  border: solid rgba(242, 242, 242, 0);
  border-width: 0 0 3px 0;
  margin-bottom: 0;
  margin-right: 1px;
  transition: all 0.5s; }

.navLinkBasket {
  border: solid rgba(242, 242, 242, 0);
  color: #ffffff;
  transition: 0.5s;
  text-decoration: none!important; }

.navLinkBasket i {
  margin-right: 10px;
  height: 15px;
  transition: 0.5s; }

.navLinkBasket:hover i, .navLinkBasket:hover {
  color: #ff9143; }

.navLink:hover, .navActivLink {
  text-decoration: none;
  border: solid #ff943d;
  border-width: 0 0 3px 0;
  color: #ff943d; }

.navBar {
  margin: 0 !important;
  padding: 0 !important;
  background: linear-gradient(rgb(158, 156, 156), #343a40, #343a40, #343a40); }

.toggler {
  margin: 2px auto; }


@media (max-width: 767px) {
  .navLink {
    border: solid transparent;
    text-align: center;
    transition: all 0.5s; }


  .navLink:hover {
    border: solid transparent;
    background-color: rgba(141, 155, 164, 0.16); }

  .navActivLink {
    border: solid transparent;
    background-color: rgba(141, 155, 164, 0.27); } }





